































































.logo {
  height: 200px;
  width: 200px;
  display: block;
  margin: 200px auto 60px;
}
input {
  padding: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid #d6d6d6;
  margin-left: 40px;
  width:90%;
}
.btn-warpper {
  margin: 50px 30px;
  button {
    margin: 0;
    text-align: center;
    height: 80px;
    padding: 15px;
    border-radius: 20px;
    color: #fff;
    border: 0;
    background-color: #df2d2d;
    font-size: 40px;
    vertical-align: middle;
    line-height: 48px;
    box-sizing: border-box;
    background-color: #df2d2d;
    width: 100%;
  }
  .button-hover {
    background: #df2d2d;
    color: #fff;
  }
}
.register {
  text-align: center;
  color: #fe8c00;
}
.home {
  text-align: center;
  color: #fe8c00;
}
.tab-warpper {
  display: flex;
  justify-content: space-between;
  margin: 0 40px;
}
